.app {
  text-align: center;
}

.check{
  display: grid;
  background: #61dafb;
  grid-template-columns: 50px 250px 250px 250px;
  grid-template-rows: 50px calc(100vh - 70px);
  gap: 20px;
  grid-template-areas:
    "logo header header header"
    "sidebar content1 content2 content3";
  overflow-x: auto;  
}

.header{
  grid-area: header; 
  grid-column-start: 2;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  background-color: #cebb14;
  border-radius: 20px;
}

.sidebar{
  grid-area: sidebar;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  background-color: #b40c36;
  border-radius: 20px;
}

.content1{
  grid-area: content;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
  border-radius: 10px; 
  box-shadow: 12px 12px 12px rgba(0, 0, 0, 0.1) inset, -12px -12px 12px rgba(58, 57, 57, 0.1) inset; 
}

.content2{
  grid-area: content;
  background-color: #0c4eb4;
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 3;
}

.content3{
  grid-area: content;
  background-color: #b40ca1;
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 2;
  grid-row-end: 3;
}
